import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ServerApi } from '../config';

/*
  Generated class for the RestProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class RestProvider {
  contentHeader: HttpHeaders = new HttpHeaders({"Content-Type": "application/json"});

  SEND_REQUEST_NEW_PASSWORD_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.SEND_REQUEST_NEW_PASSWORD;
  CHECK_RESET_PASSWORD_CODE_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.CHECK_RESET_PASSWORD_CODE;
  SET_NEW_PASSWORD_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.SET_NEW_PASSWORD;
  USER_GET_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.GET_USER_INFO;
  PRODUCT_SEARCH_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.SEARCH_PRODUCT;
  SEND_CART_ORDER_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.STORE_CART;
  GET_CART_HISTORY_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.GET_ORDER_HISTORY;
  GET_CART_DETAIL_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.GET_ORDER_DETAIL;
  GET_WALLET_HISTORY_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.GET_WALLET_INFO;
  INIT_SUGGEST_PRODUCT_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.INIT_SUGGEST_PRODUCT;
  UPLOAD_SUGGEST_PRODUCT_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.UPLOAD_SUGGEST_PRODUCT;
  UPLOAD_SUGGEST_PRODUCT_ID_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.UPLOAD_SUGGEST_PRODUCT_ID;
  INIT_COLOR_MIX_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.INIT_COLOR_MIX;
  UPLOAD_COLOR_MIX_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.UPLOAD_COLOR_MIX;
  UPLOAD_COLOR_MIX_ID_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.UPLOAD_COLOR_MIX_ID;
  NOTIFICATION_STORE_USER_ID_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.NOTIFICATION_STORE_USER_ID;
  NOTIFICATION_GET_ALL_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.NOTIFICATION_GET_ALL;
  NOTIFICATION_COUNT_UNREAD_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.NOTIFICATION_COUNT_UNREAD;
  NOTIFICATION_MARK_AS_READ_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.NOTIFICATION_MARK_AS_READ;
  NOTIFICATION_MARK_ALL_AS_READ_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.NOTIFICATION_MARK_ALL_AS_READ;

  constructor(public http: HttpClient) {}

  /**
   * Get authenticated user info
   */
  getUserInfo() {
    let user_token = localStorage.getItem('token');
    return new Promise((resolve, reject) => {
      this.http.get(this.USER_GET_URL + '?token=' + user_token, {headers: this.contentHeader})
      .subscribe(
        (res) => {
          resolve(res)
        },
        (err) => {
          reject(err)
        }
      );
    });
  }

  /**
   * Search products with name contain search term
   */
  searchProduct(searchTerm) {
    let user_token = localStorage.getItem('token');
    return new Promise((resolve, reject) => {
      this.http.post(this.PRODUCT_SEARCH_URL + '?token=' + user_token, {searchTerm: searchTerm} ,{headers: this.contentHeader})
      .subscribe(
        (res) => {
          resolve(res)
        },
        (err) => {
          reject(err)
        }
      );
    });
  }

  /**
   * Send cart order request
   */
  sendCartOrder(productCart, note) {
    let user_token = localStorage.getItem('token');
    return new Promise((resolve, reject) => {
      this.http.post(this.SEND_CART_ORDER_URL + '?token=' + user_token, JSON.stringify({productCart: productCart, note: note}) ,{headers: this.contentHeader})
      .subscribe(
        (res) => {
          resolve(res)
        },
        (err) => {
          reject(err)
        }
      );
    });
  }
  
  /**
   * Get cart order history
   */
  getOrderHistory(url?: string) {
    let user_token = localStorage.getItem('token');
    if (url === undefined) {
      url = this.GET_CART_HISTORY_URL + '?token=' + user_token;
    }
    else {
      url = url + '&&token=' + user_token;
    }
    return new Promise((resolve, reject) => {
      this.http.get(url, {headers: this.contentHeader})
      .subscribe(
        (res) => {
          resolve(res)
        },
        (err) => {
          reject(err)
        }
      );
    });
  }

  /**
   * Get cart order history detail
   */
  getOrderHistoryDetail(id: number) {
    let user_token = localStorage.getItem('token');
    return new Promise((resolve, reject) => {
      this.http.get(this.GET_CART_DETAIL_URL + '?id=' + id + '&&token=' + user_token, {headers: this.contentHeader})
      .subscribe(
        (res) => {
          resolve(res)
        },
        (err) => {
          reject(err)
        }
      );
    });
  }

  /**
   * Get wallet transaction history
   */
  getWalletHistory(url?: string) {
    let user_token = localStorage.getItem('token');
    if (url === undefined) {
      url = this.GET_WALLET_HISTORY_URL + '?token=' + user_token;
    }
    else {
      url = url + '&&token=' + user_token;
    }
    return new Promise((resolve, reject) => {
      this.http.get(url, {headers: this.contentHeader})
      .subscribe(
        (res) => {
          resolve(res)
        },
        (err) => {
          reject(err)
        }
      );
    });
  }

  /**
   * Initialize new upload session
   */
  initSuggestProduct(timestamp) {
    let user_token = localStorage.getItem('token');
    return new Promise((resolve, reject) => {
      this.http.get(this.INIT_SUGGEST_PRODUCT_URL + '?token=' + user_token + '&&timestamp=' + timestamp,{headers: this.contentHeader})
      .subscribe(
        (res) => {
          resolve(res)
        },
        (err) => {
          reject(err)
        }
      );
    });
  }

  /**
   * Post suggest product image id that uploaded
   */
  postSuggestProductId(dataUpload, timestamp) {
    let user_token = localStorage.getItem('token');
    return new Promise((resolve, reject) => {
      this.http.post(this.UPLOAD_SUGGEST_PRODUCT_ID_URL + '?token=' + user_token, JSON.stringify({ name: dataUpload.name, content: dataUpload.content, timestamp: timestamp}) ,{headers: this.contentHeader})
      .subscribe(
        (res) => {
          resolve(res)
        },
        (err) => {
          reject(err)
        }
      );
    });
  }

  /**
   * Initialize new upload session
   */
  initColorMix(timestamp) {
    let user_token = localStorage.getItem('token');
    return new Promise((resolve, reject) => {
      this.http.get(this.INIT_COLOR_MIX_URL + '?token=' + user_token + '&&timestamp=' + timestamp,{headers: this.contentHeader})
      .subscribe(
        (res) => {
          resolve(res)
        },
        (err) => {
          reject(err)
        }
      );
    });
  }

  /**
   * Post color request image id that uploaded
   */
  postColorMixId(dataUpload, timestamp) {
    let user_token = localStorage.getItem('token');
    return new Promise((resolve, reject) => {
      this.http.post(this.UPLOAD_COLOR_MIX_ID_URL + '?token=' + user_token, JSON.stringify({ name: dataUpload.name, content: dataUpload.content, timestamp: timestamp}) ,{headers: this.contentHeader})
      .subscribe(
        (res) => {
          resolve(res)
        },
        (err) => {
          reject(err)
        }
      );
    });
  }

  /**
   * Client forgot his/her password and request new
   */
  sendRequestNewPassword(email) {
    let user_token = localStorage.getItem('token');
    return new Promise((resolve, reject) => {
      this.http.post(this.SEND_REQUEST_NEW_PASSWORD_URL + '?token=' + user_token, JSON.stringify({email: email}) ,{headers: this.contentHeader})
      .subscribe(
        (res) => {
          resolve(res)
        },
        (err) => {
          reject(err)
        }
      );
    });
  }

  /**
   * Check confirm reset passoword code
   */
  checkResetPasswordCode(code, token) {
    return new Promise((resolve, reject) => {
      this.http.post(this.CHECK_RESET_PASSWORD_CODE_URL + '?token=' + token, JSON.stringify({ code: code}) ,{headers: this.contentHeader})
      .subscribe(
        (res) => {
          resolve(res)
        },
        (err) => {
          reject(err)
        }
      );
    });
  }

  /**
   * Set new passord
   */
  setNewPassword(password, password_confirmation, token) {
    return new Promise((resolve, reject) => {
      this.http.post(this.SET_NEW_PASSWORD_URL + '?token=' + token, JSON.stringify({ password: password, password_confirmation: password_confirmation}) ,{headers: this.contentHeader})
      .subscribe(
        (res) => {
          resolve(res)
        },
        (err) => {
          reject(err)
        }
      );
    });
  }

  /**
   * Upload and store onesignal user id
   */
  storeOneSignalUserId(userId) {
    let user_token = localStorage.getItem('token');
    return new Promise((resolve, reject) => {
      this.http.post(this.NOTIFICATION_STORE_USER_ID_URL + '?token=' + user_token, JSON.stringify({user_id: userId}) ,{headers: this.contentHeader})
      .subscribe(
        (res) => {
          resolve(res)
        },
        (err) => {
          reject(err)
        }
      );
    });
  }

  /**
   * Get all notification
   */
  getAllNotification(url?:string) {
    let user_token = localStorage.getItem('token');
    if (url === undefined) {
      url = this.NOTIFICATION_GET_ALL_URL + '?token=' + user_token;
    }
    else {
      url = url + '&&token=' + user_token;
    }
    return new Promise((resolve, reject) => {
      this.http.get(url,{headers: this.contentHeader})
      .subscribe(
        (res) => {
          resolve(res)
        },
        (err) => {
          reject(err)
        }
      );
    });
  }

  /**
   * Check if exist unread notification
   */
  countUnreadNotification() {
    let user_token = localStorage.getItem('token');
    return new Promise((resolve, reject) => {
      this.http.get(this.NOTIFICATION_COUNT_UNREAD_URL + '?token=' + user_token, {headers: this.contentHeader})
      .subscribe(
        (res) => {
          resolve(res)
        },
        (err) => {
          reject(err)
        }
      );
    });
  }

  /**
   * Mark notification as read
   */
  markAsRead(id) {
    let user_token = localStorage.getItem('token');
    return new Promise((resolve, reject) => {
      this.http.get(this.NOTIFICATION_MARK_AS_READ_URL + '?token=' + user_token + '&id=' + id, {headers: this.contentHeader})
      .subscribe(
        (res) => {
          resolve(res)
        },
        (err) => {
          reject(err)
        }
      );
    });
  }

  /**
   * Mark all notifications as read
   */
  markAllAsRead() {
    let user_token = localStorage.getItem('token');
    return new Promise((resolve, reject) => {
      this.http.get(this.NOTIFICATION_MARK_ALL_AS_READ_URL + '?token=' + user_token, {headers: this.contentHeader})
      .subscribe(
        (res) => {
          resolve(res)
        },
        (err) => {
          reject(err)
        }
      );
    });
  }

}



