import { HttpClient, HttpHeaders } from '@angular/common/http';
import { GlobalProvider } from './../global/global';
import { Injectable } from '@angular/core';
import { ServerApi } from '../config';
import { JwtHelper, tokenNotExpired } from 'angular2-jwt';

/*
  Generated class for the AuthProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class AuthProvider {

  LOGIN_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.API_LOGIN;
  SIGNUP_URL: string = ServerApi.SERVER_NAME[ServerApi.Environment] + "/" + ServerApi.BASE_ADDRESS.API_PREFIX + "/" + ServerApi.BASE_ADDRESS.API_REGISTER;

  contentHeader: HttpHeaders = new HttpHeaders({ "Content-Type": "application/json" });
  jwtHelper: JwtHelper = new JwtHelper();
  constructor(
      private http: HttpClient, 
      public global: GlobalProvider, 
  ) { }

  /**
   * Check if user authenticated
   * 
   * @return boolean
   */
  public authenticated() {
      return tokenNotExpired();
  }

  /**
   * Login
   * 
   * @param credentials Login information
   */
  signin(credentials) {
      return new Promise((resolve, reject) => {
          this.http.post(this.LOGIN_URL, JSON.stringify(credentials), { headers: this.contentHeader })
              .subscribe(
                  (data) => {
                      this.authSuccess(data);
                      resolve(data)
                  },
                  (err) => {
                      reject(err)
                  }
              );
      });
  }

  /**
   * Register new user
   * 
   * @param credentials Signup information
   */
  signup(credentials) {
      return new Promise((resolve, reject) => {
          this.http.post(this.SIGNUP_URL, JSON.stringify(credentials), { headers: this.contentHeader })
              .subscribe(
                  (data) => {
                      resolve(data)
                  },
                  (err) => {
                      reject(err)
                  }
              );
      });
  }

  /**
   * Logout
   */
  logout() {
      localStorage.removeItem('token');
      this.global.userInfo = null;
  }

  /**
   * Actions to take after succesful login
   * 
   * @param data information returned from server
   */
  authSuccess(data) {
      localStorage.setItem('token', data.token);
      this.global.userInfo = data.info;
  }

}



